
h4{
    cursor: pointer;
    transition: all .3s ease;
    &:hover{
        transform:translateX(5px);
    }
    
    border-bottom: 1px solid black;
}
.Home_Banner_Strell {
    display: inline-block; 
    width: 13.6px;
    height: 1px;
    border-radius: 1px;
    background-color: black;
    vertical-align: middle; 
    position: relative;
    cursor: pointer;
    margin-left: 6px;
    
    z-index: 2;    
    &::before,
    &::after {
        content: '';
        position: absolute;
        right: -1px;
        width: 7px;
        height: 1px;
        background-color: black;
        border-radius: 1px;
        transform-origin: right center;
    }
    
    &::before {
        transform: rotate(45deg);
        top: 0px;
    }
    
    &::after {
        transform: rotate(-45deg);
        bottom: 0px;
    }
    
    
    transition: all 0.2s ease;
}
